 @import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

 * {
   margin: 0;
   padding: 0%;
   box-sizing: border-box;
   font-family: 'Inter', sans-serif;
 }
.text-center{
  text-align: center;
}

 body {
   color: #484848;
 }

 .fs-20 {
   font-size: 20px;
 }

 #upper {
   background: #FFCD9E;
   display: flex;
   justify-content: space-between;
   align-items: center;
   width: 100%;
   min-height: 70vh;
   padding: 50px 10vw 30px;
 }


 #upper-left {
   width: 50%;

 }

 .upperHead {
   font-size: 36px;
   color: #282828;
   line-height: 54px;
   margin-bottom: 28px;
 }

 #upper-left .main-text {
   font-size: 20px;
   padding-right: 20%;
 }

 #upper-right {
   /* width: 40%; */
 }

 .text-white {
   color: white;
 }

 .text-head {
   color: #282828;
 }

 .text {
   color: #484848;
 }

 .fw-4 {
   font-weight: 400;
 }

 .fw-5 {
   font-weight: 500;
 }

 .fw-6 {
   font-weight: 600;
 }

 .fw-7 {
   font-weight: 700;
 }

 .fw-8 {
   font-weight: 800;
 }

 .mb-28 {
   margin-bottom: 30px;
 }

 .p-5-0 {
   padding: 5px 0;
 }

 input[type="email"] {
   width: 344px;
   height: 64px;
   max-width: 100%;
   padding-left: 22px;
   background: #FFFFFF;
   border: 4px solid #000000;
   border-radius: 8px;
   font-size: 20px;
   line-height: 32px;
 }

 input[type="submit"] {
   font-size: 20px;
   line-height: 24px;
   display: flex;
   align-items: center;
   justify-content: center;
   background-color: #282828;
   width: 216px;
   max-width: 100%;
   height: 64px;
   border-radius: 8px;
   text-align: center;
   transition: all 0.2s;
   cursor: pointer;
 }

 input[type="submit"]:hover {
   background-color: transparent;
   color: #282828;
 }

 .e-size {
   width: 344px;
 }

 .formBox {
   display: flex;
   gap: 16px;
   margin-bottom: 18px;
 }

 .thankyouMsg{
  font-size: 2.5rem;
  margin-bottom: 18px;
  color: #282828;
  line-height: 54px;
 }

 /* Footer Section */

 #footer {
   display: flex;
   justify-content: space-between;
   min-height: 30vh;
   background-color: #282828;
   padding: 40px 14vw;
 }

 #footer ul {
   list-style: none;
 }

 #footer ul li {
   padding: 5px 0;
 }

 #footer a {
   text-decoration: none;
   color: white;
 }


 /* Responsive */



 @media screen and (max-width:576px) {
   #footer {
     /* flex-direction: column; */
     padding: 15px 20px;
   }
 }

 @media screen and (max-width:768px) {

   #upper {
     flex-direction: column;
     gap: 12px;
     padding: 35px 1.6rem;
     text-align: center;
   }


   #upper-left {
     width: 100%;
     order: 2;
   }

   #upper-right {
     order: 1;
     padding: 0 20px;
   }

   #upper-right svg {
     width: 100%;
   }

   #upper-left h1 {
     font-size: 26px;
     line-height: 35px;
     margin-bottom: 28px;
   }

   #upper-left .main-text {
     font-size: 1.2rem;
     padding-right: 0%;
   }

   input[type="submit"],
   input[type="email"] {
     margin: auto;
   }

   .e-size {
     width: 100%;
   }
 }

 @media screen and (max-width:400px) {
   #footer {
     padding: 15px;
   }
 }

 @media screen and (max-width:1400px) {
   #footer {
     /* flex-direction: column; */
     padding: 40px 7vw;
   }

   #upper {
     padding: 50px 5vw 30px;
   }
 }

 @media screen and (max-width:1100px) {
   .formBox {
     /* flex-wrap: wrap; */
   }

   #upper {
     gap: 50px;
   }

   #upper-left {
     width: auto;
   }

   #upper-right svg {
     width: 340px;
   }

 }

 @media screen and (max-width:1040px) {
   .formBox {
     flex-wrap: wrap;
   }
 }